nav {
    background: var(--color-bg-variant);
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    bottom: 1.5rem;
    transform: translate(-50%);
    display: flex;
    gap: 0.8rem;
    border-radius: 3rem;
    backdrop-filter: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    font-size: 1.5rem;
}

nav a:hover {
    background: rgb(24, 24, 24);
}

nav a.active {
    background: rgb(24, 24, 24);
    color: var(--color-white);
}